<template>
  <attendance-statistics :title="$t(`attendance.${ type }`)" :result="result">
    <attendance-item v-for="(attendance, index) in attendances" :key="index" :attendance="attendance" @click.native="toClassAttendance(attendance)">
    </attendance-item>
  </attendance-statistics>
</template>

<script>
import { mapState } from 'vuex'
import attendanceStatistics from '@/views/attendance/AttendanceStatistics'
import attendanceItem from '@/views/attendance/AttendanceItem'

export default {
  name: 'attendance-classes',
  components: {
    attendanceStatistics,
    attendanceItem
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0
      },
      attendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    type () {
      return this.$route.params.type
    },
    gradeId () {
      return Number(this.$route.params.gradeId)
    }
  },
  methods: {
    analysis (data) {
      let result = {
        arrived: data.arrivedStudentGradeTotal,
        total: data.studentGradeTotal
      }
      this.attendances = data.classStudentStat.map(val => {
        val.label = val.classId
        val.arrived = val.arrivedStudentClassTotal
        val.total = val.studentClassTotal
        val.precent = val.total > 0 ? val.arrived / val.total * 100 : 0
        val.proportion = val.total > 0 && result.total > 0 ? val.total / result.total * 100 : 0
        return val
      })
      this.result = result
    },
    toClassAttendance (attendance) {
      this.$router.push({ name: 'attendance-class', params: { classId: attendance.idClass } })
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('admin/student/class/attendance', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token,
      idGrade: this.gradeId,
      queryDate: ''
    })
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
