<template>
  <div class="c-attendance-class">
    <percent-circle class="c-attendance-clas__percent" :result="result" :tip="$t('attendance.arrived')" :description="description">
    </percent-circle>
    <div class="c-attendance-class__students">
      <div v-for="(student, index) in students" :key="index" class="c-attendance-student" @click="viewDetails(student)" :class="{ 'is-nonarrival': student.status === 0, 'is-leave': student.status === 1, 'is-manual': student.status === 2, 'is-recorded': student.status === 3 }">
        <div class="c-attendance-student__avatar">
          <div class="c-solid-avatar">
            <div class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ student.headUrl }` }"></div>
          </div>
        </div>
        <div class="c-attendance-student__name">{{ student.lastName }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import percentCircle from '@/components/PercentCircle.vue'

export default {
  name: 'attendance-class',
  components: {
    percentCircle
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0,
        percent: 0
      },
      description: '',
      students: []
    }
  },
  computed: {
    ...mapState(['user']),
    classId () {
      return Number(this.$route.params.classId)
    },
    url () {
      return this.user.roles === 1 ? 'admin/class/studentattendance' : 'teacher/class/studentattendance'
    }
  },
  methods: {
    analysis (data) {
      this.result = {
        arrived: data.arrivedStudentClassTotal,
        total: data.studentClassTotal,
        percent: data.studentClassTotal > 0 ? data.arrivedStudentClassTotal / data.studentClassTotal : 0
      }
      this.description = this.$t('format.classInfo', {
        gradeLevel: data.gradeLevel,
        classId: data.classId
      })
      this.students = data.studentAttendanceInfo
    },
    viewDetails (student) {
      this.$router.push({ name: 'sing-in-student', params: { studentId: student.idStudent, type: student.status === 0 ? 'manual' : ''  } })
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('teacher/class/studentattendance', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    }).request({
      token: this.user.token,
      idClass: this.classId,
      queryDate: ''
    })
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-attendance-class {
  display: flex;
  flex-direction: column;
  font-size: 0;
  &__percent {
    flex: none;
    padding-top: 24px;
  }
  &__students {
    flex: auto;
    overflow-x: hidden;
  }
}
%student-status {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
}
.c-attendance-student {
  position: relative;
  display: inline-block;
  padding: 14px 5px;
  color: #000;
  text-align: center;
  &__avatar {
    position: relative;
    display: inline-block;
    line-height: 0;
  }
  &__name {
    width: 80px;
    padding: 8px 0;
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    text-align: center;
    overflow: hidden;
  }
  &.is-nonarrival {
    opacity: 0.6;
  }
  &.is-manual {
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_manual', 39, 39);
    }
  }
  &.is-leave {
    .c-solid-avatar, .c-attendance-student__name {
      opacity: 0.6;
    }
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_leave', 39, 39);
    }
  }
  &.is-recorded {
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_normal', 39, 39);
    }
  }
}
</style>
